<template>
    <div class="main-center">
        <div class="change-date-box">
            <div class="pre-month"
                 :style="submitAble?{cursor:'not-allowed'}:{}"
                 @click="moveMonth(1)"><span class="arrow-left arrow"></span></div>
            <el-date-picker
                    :disabled="submitAble"
                    @change="changeYearMonth"
                    :format="dateFormat"
                    :value-format="dateValueFormat"
                    v-model="curDate"
                    prefix-icon="arrow-down arrow"
                    type="month">
            </el-date-picker>
            <!--<div class="full-date">
                202004<span class="arrow-down arrow"></span>
            </div>-->
            <div class="next-month"
                 :style="submitAble?{cursor:'not-allowed'}:{}"
                 @click="moveMonth(2)"><span class="arrow-right arrow"></span></div>
        </div>
        <div class="course-table"
             v-loading="submitAble">
            <div class="course-table-list">
                <div class="th">
                    <div class="td"></div>
                    <div class="td"></div>
                    <div class="td"></div>
                    <div class="td"></div>
                    <div class="td"></div>
                    <div class="td"></div>
                    <div class="td"></div>
                </div>
                <div class="tr"
                     :key="index"
                     v-for="(item,index) in tableArr">
                    <div class="td"
                         :class="{'current':subItem.checked}"
                         @click="clickTd(subItem)"
                         :key="subIndex"
                         v-for="(subItem,subIndex) in item"><span class="day"
                                                                  v-if="subItem.type==='number'">{{subItem.key}}</span>
                        <ul class="lesson-list"
                            v-if="subItem.subjects && subItem.subjects.length && subItem.subjects.length<4">
                            <li v-for="(grandItem,grandIndex) in subItem.subjects"
                                class="clear"
                                :key="grandIndex">
                                <span class="subject">{{grandItem.subject_name}}</span>
                                <span class="date fr">{{grandItem.lesson_start_time}}-{{grandItem.lesson_end_time}}</span>
                            </li>
                        </ul>
                        <ul class="lesson-list"
                            v-if="subItem.subjects && subItem.subjects.length && subItem.subjects.length>=4">
                            <li v-for="(grandItem,grandIndex) in subItem.subjects"
                                class="clear"
                                v-show="grandIndex<2"
                                :key="grandIndex">
                                <span class="subject">{{grandItem.subject_name}}</span>
                                <span class="date fr">{{grandItem.lesson_start_time}}-{{grandItem.lesson_end_time}}</span>
                            </li>
                            <el-tooltip placement="right"
                                            effect="light">
                                    <div slot="content">
                                        <ul>
                                            <li v-for="(grandItem,grandIndex) in subItem.subjects"
                                                style="margin-bottom:5px;"
                                                :key="grandIndex">
                                                <span style="margin-right:3px;font-weight: bold">{{grandItem.subject_name}}</span>
                                                <span style="color:#999">{{grandItem.lesson_start_time}}-{{grandItem.lesson_end_time}}</span>
                                            </li>
                                        </ul>
                                    </div>
                                    <li class="clear">
                                        <span class="remain">{{subItem.subjects.length}}</span>
                                        <a href="javascript:void(0)" class="fr"></a>
                                     </li>
                            </el-tooltip>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <ul class="course-list"
            v-if="lessonList.length"
            v-loading="submitAble">
            <!--<li v-for="(item) in lessonList"
                :key="item.lesson_id">
                <div class="course-box common-bor">
                    &lt;!&ndash;<span class="status"
                          :class="[getStatusClassName(item)]">{{item.lesson_status_name}}</span>&ndash;&gt;
                    <img :src="item.lesson_cover_image"
                         class="course-img"
                         :alt="item.lesson_name">
                    <div class="course-info">
                        &lt;!&ndash;<span class="lang"></span>&ndash;&gt;
                        <p class="lesson-name" :title="item.lesson_name">{{item.lesson_name}}</p>
                        <p class="course-name">{{item.course_name}}</p>
                        <p class="date">{{item.lesson_day}} {{item.lesson_start_time || '-'}}-{{item.lesson_end_time || '-'}}</p>
                        <div class="stu-info">
                            <img :src="item.teacher_image" alt="">
                            <span style="width: auto;text-align: left;">{{item.teacher_name}}</span>
                        </div>
                    </div>
                    <div class="op-box">
                        <div class="btn-box">
                            <button class="btn-yellow btn"
                                    :class="{'btn-disabled':item.previewDisabled}"
                                    @click="preview(item.previewDisabled,item)"></button>
                            <div class="mark"
                                 v-if="item.previewNotComplete"><i class="mark-txt"></i><span></span></div>
                        </div>
                        <div class="btn-box">
                            <button class="btn-yellow btn"
                                    :class="{'btn-disabled':item.reviewDisabled}"
                                    @click="review(item.reviewDisabled,item)"></button>
                            <div class="mark"
                                 v-if="item.reviewNotComplete"><i class="mark-txt"></i><span></span></div>
                        </div>
                        <div class="btn-box">
                            <button class="btn-orange btn"
                                    @click="goView('courseDetail',{lesson_id:item.lesson_id})"></button>
                        </div>
                    </div>
                </div>
            </li>-->





            <li v-for="(item) in lessonList"
                :key="item.lesson_id">
                <div class="course-box common-bor"
                     v-if="item.lesson_type!=='2'">
                    <span class="status"
                          :class="[getStatusClassName(item)]">{{item.lesson_status_name}}</span>
                    <img :src="item.lesson_cover_image"
                         class="course-img"
                         :alt="item.lesson_name">
                    <div class="course-info">
                        <!--<span class="lang"></span>-->
                        <p class="lesson-name" :title="item.lesson_name">{{item.lesson_name}}</p>
                        <p class="course-name">{{item.course_name}}</p>
                        <p class="date" v-if="item.is_one_day==='1'">{{item.lesson_day}} {{item.lesson_start_time || '-'}} - {{item.lesson_end_time || '-'}}</p>
                        <p class="date" v-if="item.is_one_day!=='1'">{{item.lesson_start_time || '-'}} - {{item.lesson_end_time || '-'}}</p>
                        <div class="stu-info" style="margin-top: 1.9rem;">
                            <img :src="item.teacher_image" alt="">
                            <span style="width: auto;text-align: left">{{item.teacher_name}}</span>
                        </div>
                    </div>
                    <div class="op-box" style="display: flex;align-items: flex-end;">
                        <!-- <div class="btn-box">
                            <button class="btn-yellow btn"
                                    :class="{'btn-disabled':item.previewDisabled}"
                                    @click="preview(item.previewDisabled,item)"></button>
                            <div class="mark"
                                 v-if="item.previewNotComplete"><i class="mark-txt"></i><span></span></div>
                        </div>
                        <div class="btn-box">
                            <button class="btn-yellow btn"
                                    :class="{'btn-disabled':item.reviewDisabled}"
                                    @click="review(item.reviewDisabled,item)"></button>
                            <div class="mark"
                                 v-if="item.reviewNotComplete"><i class="mark-txt"></i><span></span></div>
                        </div> -->
                         <div class="btn-box" v-for="(buttonItem,index) in item.button_list" :key="index">
                            <button class="btn-yellow btn"
                                    :class="{'btn-disabled':buttonItem.enable!=='1'}"
                                    @click="goViewFn(item,buttonItem)">{{buttonItem.button_name}}</button>
                            <div class="mark"
                                 v-if="buttonItem.is_complete==='0'"><i class="mark-txt">{{buttonItem.is_complete==='0'?'':''}}</i><span></span></div>
                        </div>
                        <div class="btn-box">
                            <button class="btn-yellow btn"
                                    @click="goView('courseDetail',{lesson_id:item.lesson_id})"></button>
                        </div>
                    </div>
                </div>

                <div class="course-box common-bor" v-if="item.lesson_type==='2'">
                    <span class="status"
                          :class="[getStatusClassName(item)]">{{item.lesson_status_name}}</span>
                    <img :src="item.lesson_cover_image"
                         class="course-img"
                         :alt="item.lesson_name">
                    <div class="course-info">
                        <!--<span class="lang"></span>-->
                        <p class="lesson-name" :title="item.lesson_name">{{item.lesson_name}}</p>
                        <p class="course-name">{{item.course_name}}</p>
                        <p class="date" v-if="item.is_one_day==='1'">
                            <!--2020-04-04 16:00
                            
                            2020-04-05 17:00-->
                            {{item.lesson_day}} {{item.lesson_start_time || '-'}} - {{item.lesson_end_time || '-'}}<br>
                             {{item.test_long}} </p>
                        <p class="date" v-if="item.is_one_day!=='1'">
                            {{item.lesson_start_time || '-'}} - {{item.lesson_end_time || '-'}}<br>
                             {{item.test_long}} </p>
                    </div>
                    <div class="op-box">
                        <div class="btn-box" style="margin-top: 6.1rem;">
                            <button class="btn-yellow btn"
                                    @click="goView('checkEvaluationDetail',{lesson_id:item.lesson_id})"></button>
                        </div>
                    </div>
                </div>
            </li>



        </ul>
        <div class="has-not-child-box" v-if="!lessonList.length">
            <img src="@/assets/img/empty-img.png" alt="">
            <p class="login-tit"></p>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'
    function nowDateFormat(y,m){
        let fullYear = y+''
        let fullMonth = m<10?'0'+m:m+''
        let result = fullYear+'-'+fullMonth
        return result
    }
    function getNowDate(){
        let nowDate = new Date()
        let fullYear = nowDate.getFullYear()
        let month = nowDate.getMonth() + 1
        return nowDateFormat(fullYear,month)
    }

    export default {
        data: () => ({
            tableArr: [],
            curDate:getNowDate(),//
            dateFormat:'yyyyMM',
            dateValueFormat:'yyyy-MM',
            today:new Date().getDate(), //
            lessonList:[]
        }),
        methods: {
            ...mapActions('course', ['getTimetableActions','getTimetableLessonListActions']),
            clickTd(item) {
                if(item.checked){
                    return
                }
                let _this = this
                this.tableArr.forEach((arr) => {
                    arr.forEach((subItem) => {
                        setChecked(subItem, false)
                    })
                })
                setChecked(item, true)
                if(item.subjects && item.subjects.length){
                    let idsList = item.subjects.map((mapItem)=>{return mapItem.lesson_id})
                    this.getTimetableLessonList(idsList)
                }else{
                    this.lessonList = []
                }
                function setChecked(obj, bol) {
                    if (obj.checked === undefined) {
                        _this.$set(obj, 'checked', bol)
                    } else {
                        obj.checked = bol
                    }
                }
            },
            //,  type1: type2
            moveMonth(type){
                if(this.submitAble){
                    return
                }
                let dateFormatArr = this.curDate.split('-')
                let year = Number(dateFormatArr[0])
                let month = Number(dateFormatArr[1].replace(/^0/,''))

                if(type===1){
                    month--
                    if(month<1){
                        year --
                        month = 12
                    }
                }else{
                    month++
                    if(month>12){
                        year ++
                        month = 1
                    }
                }
                this.curDate = nowDateFormat(year,month)
                this.getTimetable()
            },
            changeYearMonth(){
                this.getTimetable()
            },
            goViewFn(detail,item){
                if(item.enable==='0'){
                    return
                }else{
                    this.goView('courseStudyTypeList', {lesson_id: item.lesson_id,task_classify:item.task_classify})
                }
            },
            //
            getTimetable(init) {
                this.lessonList = []
                let dateFormatArr = this.curDate.split('-')
                let year = dateFormatArr[0]
                let month = dateFormatArr[1].replace(/^0/,'')
                this.tableArr = this.getCourseTableDataFrame(year,month,init)
                window.console.log(this.tableArr, '',year,month);
                let params = {
                    year: year,
                    month: month
                }
                this.getTimetableActions(params).then((res) => {
                    window.console.log(res, 'rrrrrrrrr')
                    this.submitAble = false
                    let {error_code, data} = res;
                    if (!error_code) {
//                        data[8] = [
//                            {
//                                lesson_end_time: "18:10",
//                                lesson_id: "11",
//                                lesson_start_time: "18:06",
//                                subject_name: ""
//                            },
//                            {
//                                lesson_end_time: "18:10",
//                                lesson_id: "12",
//                                lesson_start_time: "18:06",
//                                subject_name: ""
//                            },
//                            {
//                                lesson_end_time: "18:10",
//                                lesson_id: "13",
//                                lesson_start_time: "18:06",
//                                subject_name: ""
//                            },
//                            {
//                                lesson_end_time: "18:10",
//                                lesson_id: "14",
//                                lesson_start_time: "18:06",
//                                subject_name: ""
//                            }
//                        ]
                        let keyArr = Object.keys(data)
                        if (keyArr.length) {
                            this.tableArr.forEach((item) => {
                                item.forEach((subItem) => {
                                    keyArr.forEach((grandItem) => {
                                        if (subItem.key == grandItem) {
                                            this.$set(subItem, 'subjects', data[grandItem])
                                            if(init && subItem.key === this.today){
                                                let idsList = data[grandItem].map((mapItem)=>{return mapItem.lesson_id})
                                                this.getTimetableLessonList(idsList)
                                            }
                                        }
                                    })
                                })
                            })

                            window.console.log(this.tableArr, '9999');
                        }
                    }
                },()=>{
                    this.submitAble = false
                })
            },
            //
            getCourseTableDataFrame(y,m,init) {
                let arr = []
                // 11arr:7  12 arr:6
                let maper = {
                    1: 7,
                    2: 6,
                    3: 5,
                    4: 4,
                    5: 3,
                    6: 2,
                    0: 1,
                }
                let year = y
                let month = m
                let total = new Date(year, month, 0).getDate();//
                let monthStr = Number(month) < 10 ? ('0' + month) : month //01 02 ...10 11 12
                let day = new Date(year + '-' + monthStr + '-01').getDay()//(0)
                let row = (maper[day] + 4 * 7) < total ? 6 : 5 //51+4*76

                window.console.log(year + '', month + '', '' + day, '' + total + '', '' + row + '');
                for (let i = 0; i < row; i++) {
                    let subArr = []
                    arr.push(subArr)
                }

                let count = 0
                for (let j = 1; ; j++) {
                    let obj = {key: j, type: 'number'}
                    if(init){
                        obj.checked = (j === this.today)
                    }
                    if (arr[count]) {
                        //
                        if (j < total + 1) {
                            arr[count].push(obj)
                        } else {
                            break
                        }
                    } else {
                        //
                        break
                    }

                    if (count === 0) {
                        //maper
                        if (j > maper[day] - 1) {
                            count++
                        }
                    } else {
                        //
                        if (arr[count].length >= 7) {
                            count++
                        }
                    }

                }

                let firstList = arr[0]
                let lastList = arr[row - 1]

                //7key:'x'7
                while (firstList.length < 7) {
                    firstList.unshift({
                        key: 'x',
                        type: 'string'
                    })
                }
                while (lastList.length < 7) {
                    lastList.push({
                        key: 'x',
                        type: 'string'
                    })
                }
                return arr
            },
            //
            getTimetableLessonList(idsList){
                if(idsList && idsList.length){
                    let params = {
                        lesson_id_list:idsList
                    }
                    this.submitAble = true
                    this.getTimetableLessonListActions(params).then((res)=>{
                        this.submitAble = false
                        let {error_code,data} = res;
                        if(!error_code){
                            this.lessonList = data
                            this.setSomeUi()
                        }
                    },()=>{
                        this.submitAble = false
                    })
                }
            },
            /*getStatusClassName(item){
                let res = ''
                if(item.lesson_status === '0'){
                    if(item.lesson_status_name ===''){
                        res = 'status-not-start'
                    }else{
                        res = 'status-not-start-2'
                    }
                }else if(item.lesson_status === '1'){
                    res = 'status-classing'
                }else if(item.lesson_status === '2'){
                    res = 'status-disabled'
                }
                return res
            },*/
            getStatusClassName(item){
                let res = ''
                if(item.lesson_status === '0'){
                    res = 'status-not-start-2'
                }else if(item.lesson_status === '5'){
                    res = 'status-not-start'
                }else if(item.lesson_status === '1'){
                    res = 'status-classing'
                }else if(item.lesson_status === '2'){
                    res = 'status-disabled'
                }
                return res
            },
            //,
            setSomeUi(){
                let count = 0
                this.lessonList.forEach((item)=>{
                    if(item.lesson_status === '0'){
                        if(count===0){
                            item.lesson_status_name = ''
                        }else{
                            item.lesson_status_name = ''
                        }
                        count ++
                    }else if(item.lesson_status === '1'){
                        item.lesson_status_name = ''
                    }else if(item.lesson_status === '2'){
                        item.lesson_status_name = ''
                    }
                    let previewDisabled = this.btnDisabled(item.preview)
                    let reviewDisabled = this.btnDisabled(item.review)
                    let previewNotComplete = this.isNotComplete(item.preview,previewDisabled)
                    let reviewNotComplete = this.isNotComplete(item.review,reviewDisabled)
                    this.$set(item,'previewDisabled',previewDisabled)
                    this.$set(item,'reviewDisabled',reviewDisabled)
                    this.$set(item,'previewNotComplete',previewNotComplete)
                    this.$set(item,'reviewNotComplete',reviewNotComplete)
                })
            },
            isNotComplete(obj,btnDisabled){
                let notComplete = false
                if(btnDisabled){
                    notComplete = false
                }else{
                    if(obj){
                        notComplete = (obj.is_complete==0?true:false)
                    }else{
                        notComplete = false
                    }
                }
                return notComplete
            },
            btnDisabled(obj){
                let disabled = true
                if(obj){
                    disabled = (obj.enable==0?true:false)
                }else{
                    disabled = true
                }
                return disabled
            }
        },
        watch: {},
        mounted() {
            this.getTimetable(true)
        },
        computed: {},
        components: {},
        created() {
        }
    }
</script>

<style lang="postcss" scoped>
    .main-box .main-center .has-not-child-box{
        margin:0 auto 2rem;
    }
</style>
